// src/TonAdBanner.tsx
import React, { useEffect } from 'react'
import ReactDOM from 'react-dom'
import '../index.css'
import './TonAdBanner.css'
import {
  GetTonAd,
  OnAdClick,
  SendTonAdImpressionEvent
} from '../../utils/my_util'
import { OnAdVerifyOptions, TonAdProps } from '../../utils/models'

interface TonAdBannerProps {
  blockId: string
  onClick?: (ad: TonAdProps) => void
  onVerifySuccess?: (options: OnAdVerifyOptions) => void
  style?: React.CSSProperties
  darkMode?: boolean
  containerId?: string
}

// 原始的TonAdBanner组件
export const TonAdBannerComponent = ({
  blockId,
  onClick,
  onVerifySuccess,
  style,
  darkMode = false,
  containerId
}: TonAdBannerProps) => {
  console.log('createTonAdBanner TonAdBannerComponent', {
    blockId,
    onClick,
    onVerifySuccess,
    style,
    darkMode,
    containerId
  })

  const [ad, setAd] = React.useState<TonAdProps>()
  const [loading, setLoading] = React.useState(true)

  useEffect(() => {
    if (ad) {
      SendTonAdImpressionEvent(ad)
    }
  }, [ad])

  // 隐藏容器的函数
  const hideContainer = () => {
    if (containerId) {
      const container = document.getElementById(containerId)
      if (container) {
        container.style.display = 'none'
        console.log(
          `Container with ID "${containerId}" has been hidden due to no ad available`
        )
      }
    }
  }

  // 显示容器的函数
  const showContainer = () => {
    if (containerId) {
      const container = document.getElementById(containerId)
      if (container) {
        container.style.display = ''
        console.log(`Container with ID "${containerId}" has been shown`)
      }
    }
  }

  const getAd = async (blockId: string) => {
    try {
      setLoading(true)
      const data = await GetTonAd(blockId)
      if (data.success) {
        setAd(data.data)
        showContainer() // 有广告时显示容器
      } else {
        setAd(undefined)
        hideContainer() // 没有广告时隐藏容器
      }
    } catch (error) {
      console.error('fetch ad error:', error)
      setAd(undefined)
      hideContainer() // 出错时也隐藏容器
    } finally {
      setLoading(false)
    }
  }

  useEffect(() => {
    if (blockId) {
      getAd(blockId)
    }
  }, [blockId])

  const clickAd = (ad: TonAdProps) => {
    if (onClick) {
      onClick(ad)
    }
    OnAdClick(ad, (options) => {
      onVerifySuccess?.(options)
    })
  }
  if (loading) {
    return (
      <div
        className={`ton-ad-banner-container ${darkMode ? 'ton-ad-dark' : ''}`}
        style={style}
      >
        <div className='ton-ad-banner-content'>
          <div className='ton-ad-loading'>Loading...</div>
        </div>
      </div>
    )
  }

  if (!ad) {
    return <></>
  }

  return (
    <div
      className={`ton-ad-banner-container ${darkMode ? 'ton-ad-dark' : ''}`}
      style={style}
      key={ad?.adId}
      id={ad?.adId}
      onClick={() => clickAd(ad)}
    >
      <div className='ton-ad-banner-content'>
        {/* Left side: Image with 4:3 aspect ratio */}
        <div className='ton-ad-image-wrapper'>
          <img
            src={ad?.popupImage || ad?.image || ad?.icon}
            alt={ad?.text}
            className='ton-ad-image'
          />
        </div>

        {/* Right side: Content */}
        <div className='ton-ad-content-wrapper'>
          {/* Title */}
          <div className='ton-ad-title'>{ad?.text}</div>

          {/* Bottom controls */}
          <div className='ton-ad-controls'>
            <span className='ton-ad-label'>Ad</span>
            <button className='ton-ad-go-button'>Go</button>
          </div>
        </div>
      </div>
    </div>
  )
}

// 创建Banner的工具函数，避免React Hook冲突
export const createTonAdBanner = (
  containerId: string,
  options: TonAdBannerProps
): { destroy: () => void } => {
  console.log('createTonAdBanner', {
    containerId,
    options
  })

  if (typeof window === 'undefined' || typeof document === 'undefined') {
    console.error('Please run in browser environment!')
    return { destroy: () => {} }
  }

  const container = document.getElementById(containerId)
  if (!container) {
    console.error(`Container with ID "${containerId}" not found`)
    return { destroy: () => {} }
  }

  let root: any

  // 渲染Banner组件
  const renderBanner = () => {
    console.log('createTonAdBanner renderBanner', {
      containerId,
      options
    })
    // 兼容 React 18 的 createRoot API
    if ('createRoot' in ReactDOM) {
      // React 18
      root = (ReactDOM as any).createRoot(container)
      root.render(
        React.createElement(TonAdBannerComponent, {
          ...options,
          containerId
        })
      )
    } else {
      // React 17 及以下
      ReactDOM.render(
        React.createElement(TonAdBannerComponent, {
          ...options,
          containerId
        }),
        container
      )
    }
  }

  renderBanner()

  // 返回销毁函数
  return {
    destroy: () => {
      if (root) {
        root.unmount()
      } else {
        ReactDOM.unmountComponentAtNode(container)
      }
    }
  }
}

// 主要的TonAdBanner组件 - 直接使用原组件
export const TonAdBanner = TonAdBannerComponent

export default TonAdBanner
